.MainButton {
  min-width: 160px;
  min-height: 50px;
  height: 50px;
  border: 1px solid #1360B1;
  background-color: transparent;
  color: rgba(104, 104, 104, 0.86);
  margin: 0 15px;
  border-radius: 5px;
  font-size: 20px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  letter-spacing: 2px;
  text-align: center; }
  .MainButton:hover {
    border: 1px dotted #ffffff;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s; }
  .MainButton:nth-child(1):hover {
    color: rgba(223, 223, 223, 0.86);
    -webkit-box-shadow: 0px 0px 100px 0 #00fffd;
            box-shadow: 0px 0px 100px 0 #00fffd; }
  .MainButton:nth-child(2):hover {
    color: rgba(223, 223, 223, 0.86);
    -webkit-box-shadow: 0px 0px 100px 0 #ff2e2e;
            box-shadow: 0px 0px 100px 0 #ff2e2e; }
  .MainButton:nth-child(3):hover {
    color: rgba(223, 223, 223, 0.86);
    -webkit-box-shadow: 0px 0px 100px 0 #fffd62;
            box-shadow: 0px 0px 100px 0 #fffd62; }



